body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container{
  position: relative;
  width: 100%;
  height: 10em;
}
form{
  position: absolute;
  width: 50%;
  top: 220%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-color: aquamarine;
  box-shadow: 5px 5px 8px #aaaaaa;
}form h1{
  margin: 20px 50px ;
}
.form-content{
  margin: 20px 50px;
}
.prompt{
  margin: 20px 50px;
}
.prompt ul{
  margin: 0 20px 0 20px;
}
.field{
  margin-bottom: 10px;
}
.field input{
  width: 100%;
  outline: 0;
  border: 0;
  height: 30px;
  background-color: rgb(238, 234, 234);
  }
.field select{
  width: 100%;
  border: 0;
  height: 30px;
  outline: 0;
  background-color: rgb(238, 234, 234);
}
.field textarea{
  width: 100%;
  border: 0;
  outline: 0;
  background-color: rgb(238, 234, 234);
}
.button{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  border: none;
}
button{
  border: 0;
  width: 50%;
  height: 40px;
  cursor: pointer;
  background-color: red;
  border-radius: 10px;
}
